@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
html {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  background-image: url(/static/media/Background.d978241f.svg);
  background-repeat: no-repeat;
  background-position-x: center;
  background-size: cover;
}

body {
  height: 100%;
  width: 100%;
  margin: 0;
  font-family: -apple-system, 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Mobile attributes */
@media screen and (max-width: 800px) {
  .NavBar {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
  }

  .NavBar__list--item a {
    font-size: 1rem;
  }

  .NavBar__list--item a.middle {
    margin-right: 2rem;
    margin-left: 2rem;
  }
}
/* Desktop attributes */
@media screen and (min-width: 801px) {
  .NavBar {
    width: 30rem;
    margin-left: 2rem;
    margin-top: 2rem;
  }

  .NavBar__list--item a {
    font-size: 18px;
  }
}

.NavBar__list {
  display: flex;
  padding: 0;
  list-style: none;
  justify-content: space-around;
  align-items: center;
}

.NavBar__list--item a {
  text-decoration: none;
  font-weight: bold;
  color: black;
  transition: color 0.5s;
}

.NavBar__list--item a:hover {
  color: blueviolet;
}
.card {
  display: flex;
  justify-content: center;
  align-items: center;
}

.card__text--title {
  margin: 0;
  font-weight: bold;
}

.card__text--jobTitle {
  margin: 0;
  font-weight: 400;
  font-size: 17px;
  text-align: right;
}

.card--image img {
  width: 20rem;
}

@media screen and (max-width: 767px) {
    .card {
        flex-direction: column-reverse;
    }

    .card--image img {
      width: 12rem;
    }
}
.Bubble {
    max-width: 90%;
    padding: 10px 15px;
    border-radius: 20px;
    background-color: #E9CCFF;
    margin-bottom: 30px;
}

.title {
    padding: 5px;
    font-weight: bold;
    text-align: center;
    font-size: 20px;
}

@media screen and (max-width: 767px) {
    .title {
        font-size: 14px;
        padding: 0;
    }
    .text {
        font-size: 12px;
    }
}
.PercentageBar {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 60%;
}

.PercentageBar__properties {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.PercentageBar__border {
    width: 100%;
    height: 20px;
    background-color: #EFDAFF;
    border-radius: 10px;
}

.PercentageBar__content {
    background-color: #950EFF;
    border-radius: 10px;
    height: 100%;
}
.skillsCard__container {
  background-color: #efdaff;
  padding: 30px;
  width: 80%;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  box-shadow: 0 0 20px 1px rgb(0 0 0 / 25%);
}

.skillsCard__container h1 {
  margin-top: 0;
}

.SkillsCard {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  margin-top: 10px;
}

.skillCard {
  border-radius: 10px;
  padding-top: 40px;
  background-color: #f1f1f1;
  width: 40%;
}

.skillCard__name-logo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skillCard__name-logo--icon {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 50px;
  height: 50px;
}

.skillCard__skillset {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}

.skillCard__skillset--title {
    color:blueviolet;
}

.skillCard__sikillset--container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  flex-wrap: wrap;
}

@media screen and (max-width: 800px) {
  .skillsCard__container {
    width: 100%;
    border-radius: 0px;
    box-shadow: none;
  }

  .SkillsCard {
    flex-direction: column;
  }

  .skillCard {
    width: 80%;
  }
  .skillCard.middle {
    margin: 20px 0;
  }
}

.footer {
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;
    color: white;
    height: 80px;
}

.footer h4 {
    font-weight: 400;
}
.ProjectCard {
  background-color: #efdaff;
  width: 80%;
  height: 450px;
  border-radius: 15px;
  box-shadow: 0 0 20px 1px rgb(0 0 0 / 25%);
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  margin-bottom: 50px;
}

.ProjectCard__img {
  width: 50%;
  border-radius: 15px;
}

.ProjectCard__img img {
  width: 100%;
}

.ProjectCard__content {
  width: 30%;
}

.ProjectCard__content--text a {
  font-size: 32px;
  font-weight: bold;
  text-decoration: none;
  color: black;
  cursor: pointer;
  transition: color 0.3s;
}

.ProjectCard__content--text a:hover {
  color: blueviolet;
}

.ProjectCard__content--text a p {
  margin: 10px 0;
}

.ProjectCard__content--tech__icons {
  justify-content: center;
  align-items: center;
  display: flex;
  flex-flow: row wrap;
}

.TechIcon {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;
  background-color: #d6c7e1;
  border-radius: 10px;
  width: 60px;
  height: 60px;
}

@media screen and (max-width: 800px) {
  .ProjectCard {
    padding: 50px 0;
    height: auto;
    flex-direction: column;
  }

  .ProjectCard__img {
    width: 70%;
  }

  .ProjectCard__content {
    width: 70%;
  }

  .ProjectCard__content h1 {
    text-align: center;
  }
}

a {
  text-decoration: none;
}

.ConnectCard {
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  background-color: #efdaff;
  box-shadow: 0 0 20px 1px rgb(0 0 0 / 25%);
  border-radius: 15px;
}

.ConnectCard__social-media {
  margin: 20px 0;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
  border-radius: 10px;
  transition: all 0.2s;
}

.ConnectCard__social-media img {
  width: 70%;
}

.ConnectCard__social-media h3 {
  margin-bottom: 0;
  color: black;
}

.ConnectCard__social-media:hover {
  background-color: #d6c7e1;
}

@media screen and (max-width: 500px) {
  .ConnectCard {
    flex-direction: column;
    box-shadow: none;
    width: 100%;
    border-radius: 0;
    padding: 40px 0;
  }
}

.containerBubbles {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Bubbles {
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.skillContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 60px 0;
}

.ProjectCarousel {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.bubbleTitle {
  display: flex;
  width: 50%;
  align-content: center;
  justify-content: center;
  margin: 50px 0;
}

.contactSection {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contactSection__content {
  width: 70%;
}

.footerContainer {
  margin-top: 50px;
}

@media screen and (max-width: 500px) {
    .contactSection__content {
        width: 100%;
    }

    .footerContainer {
        margin-top: 0;
    }
}

